import React from 'react'
import VideoCard from './VideoCard'
import axios from 'axios';
import {Spinner} from 'react-bootstrap'

function Suggestion({tags}) {
    const [suggestion,setSuggestion] = React.useState(null);

    React.useEffect(()=>{
        var url = 'https://tesserract-green-screen.herokuapp.com/v1/video/suggest?';
        let l = tags.length;
        for(let i=0;i<l;i++){
            url += ( i == 0 ? '' : '&') + 'keyword=' + tags[i].keyword;
        } 
        axios.get(url).then((response)=>{
            setSuggestion(response.data);
        })
    },[tags]);


    return (
        <div className="d-flex flex-column"> 
        {
             suggestion ===  null ?<Spinner animation="border" variant="primary" /> : suggestion.map((item)=>{return <VideoCard data={item}/>})
        }
        </div>
    )
}

export default Suggestion
